import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				// background: '#ff0033',
				// fontColor: '#FFFFFF',
				// iconColor: '#FFFFFF',
				iconText: '类目详情' //导航栏文字
			},
			params: {
				cateId: '', //5034
				page: 1,
				size: 10
			}, //分类详情参数
			shopData: {}, //分类详情数据
			shopList: [], //分类详情列表
			loading: true
		}
	},
	onLoad(options) {
		this.navigationBarStyle.iconText = options.category
		this.params.cateId = options.cateId; //5034
		this.getCagetogyList()
	},
	onShow() {

	},
	//到达页面底部时触发的事件
	onReachBottom() {
		if (this.shopList.length >= this.shopData.total) {
			return;
		}
		this.params.page++;
		this.getCagetogyList()
	},
	methods: {
		initData() {
			//重置分页参数
			this.shopData = {}
			this.shopList = []
			this.params.page = 1
			this.getCagetogyList();
		},
		//获取分类下的商品列表
		getCagetogyList() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.categoryList, this.params, false).then((res) => {
				that.loading = false
				that.shopData = res.data
				if(that.params.page == 1) {
					that.shopList = res.data.data
				} else {
					that.shopList = that.shopList.concat(res.data.data)
				}
			});
		},
		shopDetailPage(item) {
			this.$store.commit('setGoodsDetail', item)
			this.$turnPage('/pages/index/business/shop-detail', 'navigateTo')
		}
	}
}
